import Link from 'next/link'
import { notFound } from 'next/navigation'
import { prisma } from '@/lib/db/prisma'
import { Card } from '@/components/ui/Card'
import { Badge } from '@/components/ui/Badge'
import { Button } from '@/components/ui/Button'
import { CopyButton } from '@/components/scripts/CopyButton'
import { DeleteScriptButton } from '@/components/scripts/DeleteScriptButton'

export default async function ScriptDetailPage({
  params,
}: {
  params: Promise<{ id: string }>
}) {
  const script = await prisma.savedScript.findFirst({
    where: {
      id: (await params).id,
      deletedAt: null
    },
    include: {
      project: true,
      format: true,
    },
  })

  if (!script) {
    notFound()
  }

  // Parse content JSON to get sections
  const content = JSON.parse(script.content) as Record<string, string>
  const sections = Object.keys(content)
  const fullScriptText = sections.map((section) => content[section]).join('\n\n')

  return (
    <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div className="flex justify-between items-start mb-8">
        <div>
          <h1 className="text-3xl font-bold text-neutral-900 mb-3">
            {script.title}
          </h1>
          <div className="flex gap-2">
            <Badge variant="default">{script.project.name}</Badge>
            {script.format && (
              <Badge variant="success">{script.format.name}</Badge>
            )}
          </div>
        </div>
        <div className="flex gap-3">
          <DeleteScriptButton scriptId={script.id} />
        </div>
      </div>

      <div className="space-y-6">
        <Card>
          <div className="space-y-4">
            {sections.map((section, index) => (
              <div key={section} className={index > 0 ? 'pt-4 border-t border-neutral-200' : ''}>
                <div className="flex items-center justify-between mb-2">
                  <h2 className="text-lg font-semibold text-neutral-900 uppercase">
                    {section}
                  </h2>
                  <CopyButton text={content[section]} label={`Copy ${section}`} />
                </div>
                <div className={`${index === 0 ? 'bg-neutral-100 border border-neutral-300' : 'bg-neutral-50 border border-neutral-200'} rounded-md p-4`}>
                  <p className="text-neutral-900 whitespace-pre-wrap">{content[section]}</p>
                </div>
              </div>
            ))}
          </div>

          <div className="mt-6 pt-6 border-t border-neutral-200">
            <div className="flex items-center justify-between">
              <CopyButton
                text={fullScriptText}
                label="Copy Full Script"
              />
            </div>
          </div>
        </Card>

        {script.format && (
          <Card>
            <h2 className="text-lg font-semibold text-neutral-900 mb-4">
              Format Details
            </h2>
            <div className="space-y-3">
              <div>
                <p className="text-sm font-medium text-neutral-500">Structure</p>
                <p className="text-sm text-neutral-900">{script.format.structure}</p>
              </div>
              <div>
                <p className="text-sm font-medium text-neutral-500">
                  Visual Description
                </p>
                <p className="text-sm text-neutral-900">
                  {script.format.visualDescription}
                </p>
              </div>
            </div>
          </Card>
        )}

        <div className="text-sm text-neutral-500">
          Saved on {new Date(script.savedAt).toLocaleDateString()} at{' '}
          {new Date(script.savedAt).toLocaleTimeString()}
        </div>
      </div>
    </div>
  )
}
